




























import { Component, Vue, Watch } from 'vue-property-decorator'
@Component({})
export default class CustomVideo extends Vue {
  sourceType: 'video'|'audio' = 'video';
  isMounted = false;
  get sourceElement ():Vue | Element | (Vue | Element)[] | undefined {
    if (!this.isMounted) return undefined
    return this.sourceType === 'video' ? this.$refs.customVideo : this.$refs.customAudio
  }

  videoSrc = 'https://zego-public.oss-cn-shanghai.aliyuncs.com/sdk-doc/assets/big_buck_bunny.mp4'
  @Watch('sourceElement')
  onElChange (val: Element):void {
    this.$emit('mediaElement', val)
  }

  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  changeFile (e:any) :void{
    const customVideo = this.$refs.customVideo as HTMLVideoElement
    if (customVideo) {
      console.log('changeFile', e, this)
      const video = e.srcElement.files[0]
      const url = URL.createObjectURL(video)
      this.videoSrc = url
    }
  }

  mounted ():void {
    this.isMounted = true
    this.$emit('mediaElement', this.sourceElement)
  }
}
