






























































































































































































import { Component, Vue, Watch } from 'vue-property-decorator'
import VERTC, {
  IRTCEngine,
  RoomProfileType,
  StreamIndex
} from '@volcengine/rtc'
import PlayCard from '@/components/PlayCard.vue'
import MediaPlayer from './MediaPlayer.vue'
import DeviceManager from './DeviceManager.vue'
import CustomVideo from '../Express/CustomVideo.vue'
import {
  getStreamQuality,
  setStreamQuality,
  getDemoConfig,
  setDemoConfig
} from '@/common/streamUtils'
// import { ZegoExpressEngine } from 'zego-express-engine-webrtc'
import { mixins } from 'vue-class-component'
import RtcDemoMixin from '@/common/rtcDemoMixin'
// eslint-disable-next-line @typescript-eslint/ban-ts-comment

interface QualityStats {
  bitrate: number;
  width: number;
  height: number;
  frameRate: number;
  time: number;
}
interface RemoteStreamItem {
  streamID: string;
  stream?: any;
  qualityList: QualityStats[];
  videoStatus?: string;
  audioStatus?: string;
  currentStats: any;
  videoTrack?: MediaStreamTrack;
  audioTrack?: MediaStreamTrack;
  source?: 'custom' | 'screen' | 'camera';
}
@Component({
  components: { MediaPlayer, DeviceManager, PlayCard, CustomVideo }
})
export default class Volc extends mixins(RtcDemoMixin) {
  timer = 0;
  columnNumber = 3;
  // 基本信息
  rtc: IRTCEngine | undefined = undefined;
  appID = '';
  // 房间状态
  isLogin = false;
  userID = 'sample' + Date.now();
  roomID = '979797';
  token = '';
  roomState = 'DISCONNECTED';

  // 折叠款
  openNames = [];
  // 流状态
  publishState = 'DISCONNECTED';

  localTracks: {
    videoTrack?: MediaStreamTrack;
    audioTrack?: MediaStreamTrack;
  } = {
    videoTrack: undefined,
    audioTrack: undefined
  };

  qualityStatsList: QualityStats[] = [];
  public cameraInput: MediaDeviceInfo | undefined = undefined;
  public micInput: MediaDeviceInfo | undefined = undefined;
  audioState = true;
  videoState = true;

  remoteStreamMap: { [key: string]: RemoteStreamItem } = {};
  // 远端流列表
  get remoteStreamList (): RemoteStreamItem[] {
    const list = Object.values(this.remoteStreamMap)
    return list
  }

  // 侧拉框
  drawerVisible = false;

  statsTimer?: number;
  currentStats = {
    bytesSent: 0,
    framesSent: 0,
    time: 0
  };

  saveToken (): void {
    localStorage.setItem('volc-token', this.token + '')
  }

  saveAppID (): void {
    localStorage.setItem('volc-appid', this.appID + '')
  }

  saveUserID (): void {
    localStorage.setItem('volc-userid', this.userID + '')
  }

  resetEnv (): void {
    localStorage.setItem('volc-token', this.token + '')
    localStorage.setItem('volc-appid', this.appID + '')
    this.$router
      .replace({
        query: { userID: this.userID, token: this.token, appID: this.appID }
      })
      .catch(() => ({}))
    location.reload()
  }

  onVideoTrackChange (): void {
    this.localTracks.videoTrack =
      this.rtc?.getLocalStreamTrack(StreamIndex.STREAM_INDEX_MAIN, 'video') ||
      this.rtc?.getLocalStreamTrack(StreamIndex.STREAM_INDEX_SCREEN, 'video')
  }

  onAudioTrackChange (): void {
    this.localTracks.audioTrack =
      this.rtc?.getLocalStreamTrack(StreamIndex.STREAM_INDEX_MAIN, 'audio') ||
      this.rtc?.getLocalStreamTrack(StreamIndex.STREAM_INDEX_SCREEN, 'audio')
  }

  onVideoStateChange (val: boolean): void {
    this.videoState = val
  }

  onAudioStateChange (val: boolean): void {
    this.audioState = val
  }

  onCameraChange (val?: MediaDeviceInfo): void {
    this.cameraInput = val
    console.log('onCameraChange', this.cameraInput)
  }

  onMicChange (val?: MediaDeviceInfo): void {
    this.micInput = val
    console.log('onMicChange', this.micInput)
  }

  isInit = false;
  initSDK (): void {
    this.isInit = true
    // TRTC 只支持h264
    const rtc: IRTCEngine = VERTC.createEngine(this.appID)
    this.rtc = (window as any).rtc = rtc
    VERTC.enableDevices()
    rtc.off(VERTC.events.onUserPublishStream)
    rtc.on(VERTC.events.onUserPublishStream, async ({ userId, mediaType }) => {
      if (!this.demoConfig.autoPlay) {
        return console.warn('not auto play ', userId)
      }
      const startTime = performance.now()
      try {
        await rtc.subscribeStream(userId, mediaType)
      } catch (error) {
        console.error(error)
        throw error
      }
      this.$set(this.playRecordData, userId, {
        playCostTime: performance.now() - startTime
      })
      const videoTrack = rtc.getRemoteStreamTrack(
        userId,
        StreamIndex.STREAM_INDEX_MAIN,
        'video'
      )
      const audioTrack = rtc.getRemoteStreamTrack(
        userId,
        StreamIndex.STREAM_INDEX_MAIN,
        'audio'
      )
      console.log('remoteTrack', userId, audioTrack, videoTrack)
      const remoteStream: RemoteStreamItem = {
        streamID: userId,
        currentStats: {},
        qualityList: [],
        videoTrack,
        audioTrack,
        source: 'camera'
      }

      this.$set(this.remoteStreamMap, userId, remoteStream)
    }) // 收到 `VERTC.events.onUserPublishStream` 事件后进行相应处理

    rtc.on(VERTC.events.onUserPublishScreen, async ({ userId, mediaType }) => {
      if (!this.demoConfig.autoPlay) {
        return console.warn('not auto play ', userId)
      }
      const startTime = performance.now()

      await rtc.subscribeScreen(userId, mediaType).catch((e) => {
        console.error(e)
      })
      this.$set(this.playRecordData, userId, {
        playCostTime: performance.now() - startTime
      })
      const videoTrack = rtc.getRemoteStreamTrack(
        userId,
        StreamIndex.STREAM_INDEX_SCREEN,
        'video'
      )
      const audioTrack = rtc.getRemoteStreamTrack(
        userId,
        StreamIndex.STREAM_INDEX_SCREEN,
        'audio'
      )
      console.log('remoteTrack', userId, audioTrack, videoTrack)
      const remoteStream: RemoteStreamItem = {
        streamID: userId,
        currentStats: {},
        qualityList: [],
        videoTrack,
        audioTrack,
        source: 'screen'
      }

      this.$set(this.remoteStreamMap, userId, remoteStream)
    }) // 收到 `VERTC.events.onUserPublishStream` 事件后进行相应处理

    rtc.off(VERTC.events.onUserUnpublishStream)
    rtc.on(VERTC.events.onUserUnpublishStream, async ({ userId }) => {
      // 不用自己停止拉流，SDK 自动停止
      // await rtc.unsubscribeStream(userId, 3).catch(e => {
      //   alert(e)
      // STREAM_NOT_EXIST
      // })
      this.$delete(this.remoteStreamMap, userId)
    })
    rtc.off(VERTC.events.onLocalStreamStats)
    rtc.on(VERTC.events.onLocalStreamStats, (stats) => {
      const { videoStats } = stats
      const {
        sentKBitrate,
        sentFrameRate,
        encodedFrameWidth,
        encodedFrameHeight
      } = videoStats
      this.qualityStatsList.push({
        bitrate: sentKBitrate,
        width: encodedFrameWidth,
        height: encodedFrameHeight,
        frameRate: sentFrameRate,
        time: Date.now()
      })
      console.warn('onLocalStreamStats: ', stats)
    })
    rtc.off(VERTC.events.onRemoteStreamStats)
    rtc.on(VERTC.events.onRemoteStreamStats, (stats) => {
      const playStream = this.remoteStreamMap[stats.userId]
      if (!playStream) return
      const { videoStats } = stats
      const { receivedKBitrate, decoderOutputFrameRate, width, height } =
        videoStats
      playStream.qualityList.push({
        bitrate: receivedKBitrate,
        width: width,
        height: height,
        frameRate: decoderOutputFrameRate,
        time: Date.now()
      })
      console.warn('onRemoteStreamStats', stats)
    })
  }

  async loginRoomAndPublish (): Promise<void> {
    await this.loginRoom()
    await this.startPublishing()
  }

  async loginRoom (): Promise<void> {
    const { rtc } = this
    if (!rtc) return
    const config = {
      appId: this.appID,
      roomId: this.roomID,
      token: this.token,
      uid: this.userID
    }
    try {
      if (this.roomState === 'CONNECTED') return console.error('已登录')
      this.roomState = 'CONNECTING'
      await rtc
        .joinRoom(
          config.token,
          config.roomId,
          {
            userId: config.uid
          },
          {
            isAutoPublish: false, // 采集音视频时自动发布到房间
            isAutoSubscribeAudio: false, // 自动订阅音频
            isAutoSubscribeVideo: false, // 自动订阅视频
            roomProfileType: RoomProfileType.communication // 普通音视频通话模式，例如，语音聊天室
          }
        )
        .catch((e) => {
          alert('进房错误:' + JSON.stringify(e))
          throw e
        })
      this.roomState = 'CONNECTED'
    } catch (e: any) {
      console.error('进房错误：', e.message)
    }
    this.isLogin = true
    // this.startStatsTimer()
  }

  customMediaEl?: HTMLMediaElement;
  async startPreview (): Promise<void> {
    console.log('startPreview', this.cameraInput, this.micInput)
    const { rtc } = this
    if (!rtc) return

    const {
      // videoQuality,
      width,
      height,
      frameRate,
      bitrate,
      videoOptimizationMode,
      source
    } = this.videoQuality
    if (width && height && frameRate && bitrate) {
      if (source === 'camera') {
        await rtc.setVideoEncoderConfig({
          width,
          height,
          frameRate,
          maxKbps: bitrate
        })
      } else if (source === 'screen') {
        await rtc.setScreenEncoderConfig({
          width,
          height,
          frameRate,
          maxKbps: bitrate,
          contentHint: videoOptimizationMode
        })
      }
    }

    try {
      if (source === 'screen') {
        await rtc.startScreenCapture({ enableAudio: true })
        this.localTracks.videoTrack = rtc.getLocalStreamTrack(
          StreamIndex.STREAM_INDEX_SCREEN,
          'video'
        )
        this.localTracks.audioTrack = rtc.getLocalStreamTrack(
          StreamIndex.STREAM_INDEX_SCREEN,
          'audio'
        )
      } else {
        this.videoState &&
          (await rtc.startVideoCapture(this.cameraInput?.deviceId))
        this.localTracks.videoTrack = rtc.getLocalStreamTrack(
          StreamIndex.STREAM_INDEX_MAIN,
          'video'
        )
        this.audioState &&
          (await rtc.startAudioCapture(this.micInput?.deviceId))
        this.localTracks.audioTrack = rtc.getLocalStreamTrack(
          StreamIndex.STREAM_INDEX_MAIN,
          'audio'
        )
      }
    } catch (e) {
      alert('采集失败:' + JSON.stringify(e))
      throw e
    }
  }

  async startPublishing (): Promise<void> {
    console.log('startPublishing')
    setStreamQuality(this.videoQuality)
    const { rtc } = this
    if (!rtc) return
    if (!this.localTracks.videoTrack || !this.localTracks.audioTrack) {
      await this.startPreview()
    }

    // 如果调用 joinRoom 时在 config 中设置了自动发布，这里就不需要发布了
    this.publishState = 'PUBLISH_REQUESTING'

    const startTime = performance.now()
    // if(this.publishS)
    if (this.videoQuality.source === 'screen') {
      await rtc.publishScreen(3).catch((e) => {
        alert('推流失败:' + JSON.stringify(e))
        this.publishState = 'NO_PUBLISH'
        throw e
      })
    } else {
      await rtc.publishStream(3).catch((e) => {
        alert('推流失败:' + JSON.stringify(e))
        this.publishState = 'NO_PUBLISH'
        throw e
      })
    }
    this.$set(
      this.recordData,
      'publishCostTime',
      performance.now() - startTime
    )

    this.publishState = 'PUBLISHING'
    this.qualityStatsList = []
  }

  async stopPublishing (): Promise<void> {
    const { rtc } = this
    if (!rtc) return
    if (this.publishState === 'CONNECTED') {
      await rtc.unpublishStream(3)
    }
    this.publishState = 'NO_PUBLISH'
    if (this.localTracks.videoTrack) {
      await rtc.stopVideoCapture()
      await rtc.stopScreenCapture()
      this.localTracks.videoTrack = undefined
    }
    if (this.localTracks.audioTrack) {
      await rtc.stopAudioCapture()
      this.localTracks.audioTrack = undefined
    }
  }

  async logoutRoom (): Promise<void> {
    const { rtc } = this
    if (!rtc) return
    this.remoteStreamList.forEach((stream) => {
      rtc.unsubscribeStream(stream.streamID, 3)
    })
    this.remoteStreamMap = {}
    await this.stopPublishing()
    await rtc.leaveRoom()
    this.isLogin = false
    this.roomState = 'DISCONNECTED'
    this.publishState = 'NO_PUBLISH'
  }

  async playCustomStream (streamID?: string): Promise<void> {
    const userId = streamID || this.playStreamFormValue.streamID
    const index = this.playStreamFormValue.index as StreamIndex
    if (!this.isLogin) return alert('not login')
    if (!userId || isNaN(index as any)) throw new Error('play error')
    const addItem = this.remoteStreamMap[userId]
    if (addItem) throw new Error('stream already exist')
    const startTime = performance.now()
    const { rtc } = this
    if (!rtc) return
    try {
      if (index === 1) {
        await rtc.subscribeScreen(userId, 3)
      } else {
        await rtc.subscribeStream(userId, 3)
      }
    } catch (error) {
      alert(error)
      throw error
    }
    this.$set(this.playRecordData, userId, {
      playCostTime: performance.now() - startTime
    })
    const videoTrack = rtc.getRemoteStreamTrack(
      userId,
      index,
      'video'
    )
    const audioTrack = rtc.getRemoteStreamTrack(
      userId,
      index,
      'audio'
    )
    console.log('remoteTrack', userId, audioTrack, videoTrack)
    const remoteStream: RemoteStreamItem = {
      streamID: userId,
      currentStats: {},
      qualityList: [],
      videoTrack,
      audioTrack,
      source: index === 1 ? 'screen' : 'camera'
    }

    this.$set(this.remoteStreamMap, userId, remoteStream)
  }

  onAutoPlayChange (): void {
    setDemoConfig(this.demoConfig)
    location.reload()
  }

  created (): void {
    console.log('volc created')
    this.userID =
      (this.$route.query.userID as string) ||
      localStorage.getItem('volc-userid') ||
      ''
    this.token =
      (this.$route.query.token as string) ||
      localStorage.getItem('volc-token') ||
      ''
    this.appID =
      (this.$route.query.appID as string) ||
      localStorage.getItem('volc-appid') ||
      ''
    // this.$router.params =
    this.$router
      .replace({
        query: { userID: this.userID, token: this.token, appID: this.appID }
      })
      .catch(() => ({}))
    this.initSDK()
  }

  async mounted (): Promise<void> {
    document.title = 'Volc RTC'
    if (document.body.clientWidth < 450) this.columnNumber = 1
    this.videoQuality = getStreamQuality() || this.videoQuality
    this.demoConfig = getDemoConfig() || this.demoConfig
    console.log('mounted')
  }

  destroyed (): void {
    this.logoutRoom()
    this.rtc && VERTC.destroyEngine(this.rtc)
  }
}
