











































































































































































































import { Component, Vue, Watch } from 'vue-property-decorator'
import AgoraRTC, {
  IAgoraRTCClient,
  UID,
  IRemoteAudioTrack,
  IRemoteVideoTrack,
  ILocalVideoTrack,
  ILocalAudioTrack,
  IAgoraRTCRemoteUser
} from 'agora-rtc-sdk-ng'
import MediaPlayer from './MediaPlayer.vue'
import PlayCard from '@/components/PlayCard.vue'
import DeviceManager, { DeviceInfo } from './DeviceManager.vue'
import CustomVideo from '../Express/CustomVideo.vue'
import {
  getStreamQuality,
  setStreamQuality,
  getDemoConfig,
  setDemoConfig
} from '@/common/streamUtils'
import { mixins } from 'vue-class-component'
import RtcDemoMixin from '@/common/rtcDemoMixin'

interface QualityStats {
  bitrate: number;
  width: number;
  height: number;
  frameRate: number;
  time: number;
}
interface RemoteStreamItem {
  streamID: UID;
  videoTrack?: IRemoteVideoTrack;
  audioTrack?: IRemoteAudioTrack;
  qualityList: QualityStats[];
  videoStatus?: string;
  audioStatus?: string;
  // stream?: MediaStream;
}
@Component({
  components: { PlayCard, DeviceManager, MediaPlayer, CustomVideo }
})
export default class Agora extends mixins(RtcDemoMixin) {
  columnNumber = 3;
  rtc?: IAgoraRTCClient;
  appID = ''; // 'd3f084a4a39041c79102bfba8e8d5f6e';
  isLogin = false;
  userID: UID = Math.round(Math.random() * 10000);
  roomID = '979797';
  token = '';
  roomState = 'DISCONNECTED';
  // 流状态
  localStream?: MediaStream;
  QualityStatsList: QualityStats[] = [];
  cameraInput?: DeviceInfo;
  micInput?: DeviceInfo;
  audioState = true;
  videoState = true;
  localTracks: {
    videoTrack?: ILocalVideoTrack;
    audioTrack?: ILocalAudioTrack;
  } = {
    videoTrack: undefined,
    audioTrack: undefined
  };

  // 折叠款
  openNames = ['2'];

  publishState = 'NO_PUBLISH';
  playingRemoteStreamList: RemoteStreamItem[] = [];
  remoteStreamMap: { [key: string]: IAgoraRTCRemoteUser } = {};
  get remoteStreamList (): IAgoraRTCRemoteUser[] {
    const list = Object.values(this.remoteStreamMap)
    return list
  }

  publishQualityList: QualityStats[] = [];

  get streamID (): UID {
    return this.userID
  }

  saveToken (): void {
    localStorage.setItem('agora-token', this.token)
  }

  saveAppID (): void {
    localStorage.setItem('agora-appid', this.appID)
  }

  statsTimer?: number;
  startStatsTimer (): void {
    clearInterval(this.statsTimer)
    this.statsTimer = window.setInterval(() => {
      if (!this.rtc) return
      if (this.localTracks.videoTrack && this.publishState === 'PUBLISHING') {
        const videoStats = this.rtc.getLocalVideoStats()
        // const audioStats = this.localTracks.audioTrack ? this.rtc.getLocalAudioStats() : {}
        const {
          sendResolutionWidth,
          sendResolutionHeight,
          sendBitrate,
          sendFrameRate
        } = videoStats
        this.QualityStatsList.push({
          bitrate: sendBitrate / 1024,
          width: sendResolutionWidth,
          height: sendResolutionHeight,
          frameRate: sendFrameRate || 0,
          time: Date.now()
        })
      }
      if (this.playingRemoteStreamList.length) {
        const remoteStats = this.rtc.getRemoteVideoStats()
        this.playingRemoteStreamList.forEach((item) => {
          const videoStats = remoteStats[item.streamID]
          if (videoStats) {
            const {
              receiveResolutionWidth,
              receiveResolutionHeight,
              receiveBitrate,
              receiveFrameRate
            } = videoStats
            item.qualityList.push({
              bitrate: receiveBitrate / 1024,
              width: receiveResolutionWidth,
              height: receiveResolutionHeight,
              frameRate: receiveFrameRate || 0,
              time: Date.now()
            })
          }
        })
      }
    }, 3000)
  }

  initSDK (): void {
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    this.rtc = (window as any).rtc = AgoraRTC.createClient({
      mode: 'rtc',
      codec: 'h264'
    })
    const { rtc } = this
    this.startStatsTimer()

    rtc.on('user-published', async (user, mediaType) => {
      this.$set(this.remoteStreamMap, user.uid, user)
      if (!this.demoConfig.autoPlay) {
        return
      }
      if (!this.demoConfig.autoPlay) {
        return
      }
      try {
        const startTime = performance.now()
        await rtc.subscribe(user, mediaType)
        if (mediaType === 'video') {
          this.$set(this.playRecordData, user.uid, {
            ...this.playRecordData[user.uid] || {},
            playCostTime: performance.now() - startTime
          })
        } else {
          this.$set(this.playRecordData, user.uid, {
            ...this.playRecordData[user.uid] || {},
            audioPlayCostTime: performance.now() - startTime
          })
        }
      } catch (error) {
        console.error('subscribe failed ' + mediaType + ' ' + user.uid)
        throw error
      }
      let streamItem = this.playingRemoteStreamList.find(
        (item) => item.streamID === user.uid
      )
      if (!streamItem) {
        streamItem = {
          streamID: user.uid,
          videoTrack: undefined,
          audioTrack: undefined,
          qualityList: []
        }
        this.playingRemoteStreamList.push(streamItem)
      }
      if (streamItem) {
        if (mediaType === 'video' && user.videoTrack) {
          console.log('1111 subscribe video success', user.videoTrack)
          streamItem.videoTrack = user.videoTrack
        }
        if (mediaType === 'audio' && user.audioTrack) {
          console.log('1111 subscribe audio success', user.audioTrack)
          streamItem.audioTrack = user.audioTrack
        }
      }
    })
    rtc.on('user-unpublished', (user, mediaType) => {
      console.warn('user-unpublished')

      this.$delete(this.remoteStreamMap, user.uid)
      if (mediaType === 'video') {
        this.playingRemoteStreamList = this.playingRemoteStreamList.filter(
          (item) => {
            if (item.streamID !== user.uid) {
              return true
            } else {
              return false
            }
          }
        )
      }
    })

    rtc.on('connection-state-change', (curState, revState, reason) => {
      console.log('connection-state-change', curState, revState, reason)
      this.roomState = curState
    })
    rtc.on('live-streaming-error', (url, error) => {
      console.error('live-streaming-error', url, error)
    })
    rtc.on('live-streaming-warning', (url, error) => {
      console.error('live-streaming-warning', url, error)
    })
    rtc.on('user-joined', (user) => {
      console.error('user-joined', user)
    })
  }

  async loginRoomAndPublish (): Promise<void> {
    await this.loginRoom()

    await this.startPublishing()
  }

  async loginRoom (): Promise<void> {
    const { rtc } = this
    if (!rtc) return
    // rtc.setClientRole('host')
    this.userID = await rtc
      .join(this.appID, this.roomID, null, this.userID)
      .catch((error) => {
        console.error(error + 'loginRoom error')
        throw error
      })
    this.isLogin = true
  }

  customMediaEl?: HTMLMediaElement;
  async startPreview (): Promise<void> {
    console.log('startPreview')
    const {
      // videoQuality,
      width,
      height,
      frameRate,
      bitrate,
      videoOptimizationMode,
      source
    } = this.videoQuality
    this.videoState = !!this.cameraInput?.deviceId
    this.audioState = !!this.micInput?.deviceId
    const videoConfig = {
      cameraId: this.cameraInput?.deviceId,
      optimizationMode: videoOptimizationMode,
      encoderConfig: {
        bitrateMax: bitrate,
        bitrateMin: 0,
        frameRate,
        height,
        width
      }
    }
    if (source === 'camera') {
      this.audioState &&
        (this.localTracks.audioTrack =
          await AgoraRTC.createMicrophoneAudioTrack())
      this.videoState &&
        (this.localTracks.videoTrack = await AgoraRTC.createCameraVideoTrack(
          videoConfig
        ))
      // this.localTracks.audioTrack =
      //     await AgoraRTC.createMicrophoneAudioTrack({ encoderConfig: 'speech_low_quality' });
      // // this.localTracks.audioTrack!.setVolume(300);
      // (window as any).capAudioTrack = this.localTracks.audioTrack
    } else if (source === 'screen') {
      const tracks: any = await AgoraRTC.createScreenVideoTrack(
        {
          encoderConfig: {
            bitrateMax: bitrate,
            bitrateMin: 0,
            frameRate,
            height,
            width
          },
          optimizationMode: videoOptimizationMode
        },
        'auto'
      )
      if (tracks.length === 2) {
        this.localTracks.videoTrack = tracks[0]
        this.localTracks.audioTrack =
          tracks[1]
      } else {
        this.localTracks.videoTrack = tracks
        this.localTracks.audioTrack = ((this.audioState &&
            (this.localTracks.audioTrack =
              await AgoraRTC.createMicrophoneAudioTrack())) as ILocalAudioTrack)
      }
    } else if (source === 'custom' && this.customMediaEl) {
      const stream = (this.customMediaEl as any).captureStream()
      const videoTrack = stream.getVideoTracks()[0]
      const audioTrack = stream.getAudioTracks()[0]
      if (audioTrack) {
        this.localTracks.audioTrack = await AgoraRTC.createCustomAudioTrack({
          mediaStreamTrack: audioTrack
        })
      }
      if (videoTrack) {
        this.localTracks.videoTrack = await AgoraRTC.createCustomVideoTrack({
          mediaStreamTrack: videoTrack,
          bitrateMax: bitrate,
          bitrateMin: 0,
          optimizationMode: videoOptimizationMode
        })
      }
    } else {
      return alert('预览失败，参数错误')
    }

    // const { rtc } = this
    // if (!rtc) return
    // const { videoQuality, width, height, frameRate, bitrate } =
    //   this.videoQuality
  }

  async startPublishing (): Promise<void> {
    console.log('startPublishing')
    setStreamQuality(this.videoQuality)
    const { rtc } = this
    if (!rtc) return
    if (!this.localTracks.videoTrack && !this.localTracks.audioTrack) {
      await this.startPreview()
    }
    const tracks = []
    this.localTracks.audioTrack && tracks.push(this.localTracks.audioTrack)
    this.localTracks.videoTrack && tracks.push(this.localTracks.videoTrack)

    try {
      const startTime = performance.now()
      console.log('publish tracks', tracks)
      await rtc.publish(tracks)
      this.$set(
        this.recordData,
        'publishCostTime',
        performance.now() - startTime
      )
      console.warn('111 开始推流', tracks)
    } catch (error) {
      this.publishState = 'NO_PUBLISH'
    }
    this.publishState = 'PUBLISHING'

    this.publishQualityList = []
  }

  async stopPublishing (): Promise<void> {
    const { rtc, localTracks } = this
    if (!rtc) return

    let track: ILocalVideoTrack | ILocalAudioTrack | undefined =
      localTracks.videoTrack
    if (track) {
      track.stop()
      track.close()
      localTracks.videoTrack = undefined
    }
    track = localTracks.audioTrack
    if (track) {
      track.stop()
      track.close()
      localTracks.audioTrack = undefined
    }
    if (this.publishState !== 'NO_PUBLISH') await rtc.unpublish()
    this.publishState = 'NO_PUBLISH'
  }

  async logoutRoom (): Promise<void> {
    const { rtc } = this
    if (!rtc) return
    this.playingRemoteStreamList = []
    await this.stopPublishing()
    await rtc.leave()
    this.isLogin = false
    console.log('client leaves channel success')
  }

  async playCustomStream (streamID?: string): Promise<void> {
    streamID = streamID || this.playStreamFormValue.streamID
    if (!streamID || !this.isLogin) throw new Error('play error')
    const addItem = this.playingRemoteStreamList.find(
      (item) => item.streamID === streamID
    )
    if (addItem) throw new Error('stream already exist')
    if (!this.rtc) return

    const remoteStream = this.remoteStreamMap[streamID]

    this.$set(this.playRecordData, remoteStream.uid, {
      playStartTime: performance.now()
    })
    // 订阅远端流

    let streamItem = this.playingRemoteStreamList.find(
      (item) => item.streamID === streamID
    )
    if (!streamItem) {
      streamItem = {
        streamID,
        videoTrack: undefined,
        audioTrack: undefined,
        qualityList: []
      }
      this.playingRemoteStreamList.push(streamItem)
    }
    const startTime = performance.now()
    this.rtc.subscribe(remoteStream, 'video').then(() => {
      this.$set(this.playRecordData, remoteStream.uid, {
        ...this.playRecordData[remoteStream.uid] || {},
        playCostTime: performance.now() - startTime
      })
      streamItem!.videoTrack = remoteStream.videoTrack
      console.log('1111 subscribe video success', streamItem!.videoTrack, performance.now() - startTime)
    })
    this.rtc.subscribe(remoteStream, 'audio').then(() => {
      this.$set(this.playRecordData, remoteStream.uid, {
        ...this.playRecordData[remoteStream.uid] || {},
        audioPlayCostTime: performance.now() - startTime
      })
      streamItem!.audioTrack = remoteStream.audioTrack
      console.log('1111 subscribe audio success', streamItem!.audioTrack)
    })
  }

  // created (): void {
  // }

  onVideoStateChange (val: boolean): void {
    this.videoState = val
  }

  onAudioStateChange (val: boolean): void {
    this.audioState = val
  }

  onCameraChange (val?: MediaDeviceInfo): void {
    this.cameraInput = val
    console.log('onCameraChange', this.cameraInput)
  }

  onMicChange (val?: MediaDeviceInfo): void {
    this.micInput = val
    console.log('onMicChange', this.micInput)
  }

  onAutoPlayChange (): void {
    setDemoConfig(this.demoConfig)
    location.reload()
  }

  destroyed (): void {
    this.logoutRoom()
  }

  mounted (): void {
    document.title = 'Agora RTC'
    this.initSDK()
    if (document.body.clientWidth < 450) this.columnNumber = 1
    this.token = localStorage.getItem('agora-token') || ''
    this.appID = localStorage.getItem('agora-appid') || ''
    this.videoQuality = getStreamQuality() || this.videoQuality
    this.demoConfig = getDemoConfig() || this.demoConfig
    console.log('mounted')
  }
}
