import Axios from 'axios'
import md5 from 'md5'
import { Base64 } from 'js-base64'

const axios = Axios.create()
export async function getToken03 (
  appId: number,
  idName: string,
  roomId: string,
  time?: number
): Promise<string> {
  const res = await axios
    .post('https://sig-liveroom-admin.zego.cloud/thirdToken/get', {
      version: '03',
      appId,
      idName,
      roomId,
      privilege: {
        1: 1,
        2: 1
      },
      expire_time: time
    }).catch((e) => {
      throw e
    })

  return res.data.data.token
}

export function getToken (
  appId: number,
  idName: string
): Promise<string> {
  return new Promise((resolve, reject) => {
    const tokenUrl = 'https://wsliveroom-alpha.zego.im:8282/token'
    axios
      .get(tokenUrl, {
        params: {
          app_id: appId,
          id_name: idName,
          cgi_token: generateTokenInfo(appId)
        }
      })
      .then((result: any) => {
        resolve(result.data)
      }).catch(err => {
        reject(err)
      })
  })
}

function generateTokenInfo (appId: number): string {
  let expireTime = new Date().getTime() / 1000 + 60 * 60
  expireTime = Number.parseInt(expireTime + '')

  const serverSecret = 'a053dc0e074cf8e283128c326d018179'
  const nonce = md5(Math.random() + '')
  const checkToken = appId + serverSecret + nonce + expireTime
  const hash = md5(checkToken)

  const tokenInfo = {
    ver: 1,
    hash: hash,
    nonce: nonce,
    expired: expireTime
  }

  const token = Base64.encode(JSON.stringify(tokenInfo))

  return token
}
