































































































































































































































import { Component, Vue, Watch } from 'vue-property-decorator'
import { ZegoExpressEngine } from 'zego-express-engine-webrtc'
// import { ZegoExpressEngine } from '../../../../zego_express_web_rtc/sdk/code/zh/zego.client.web'
import { getToken03, getToken } from '@/common/http'
import PlayCard from '../../components/PlayCard.vue'
import DeviceManager from './DeviceManager.vue'
import { mixins } from 'vue-class-component'
import RtcDemoMixin from '@/common/rtcDemoMixin'

import {
  ZegoDeviceInfo,
  ZegoLocalStreamConfig
} from 'zego-express-engine-webrtc/sdk/code/zh/ZegoExpressEntity.web'
import CustomVideo from './CustomVideo.vue'
import { getStreamQuality, setStreamQuality, getDemoConfig, setDemoConfig } from '@/common/streamUtils'

interface QualityStats {
  bitrate: number;
  width: number;
  height: number;
  frameRate: number;
  time: number;
}
interface RemoteStreamItem {
  streamID: string;
  stream?: MediaStream;
  qualityList: QualityStats[];
  videoStatus?: string;
  audioStatus?: string;
}
@Component({ components: { PlayCard, DeviceManager, CustomVideo } })
export default class Express extends mixins(RtcDemoMixin) {
  columnNumber = 3;
  // 基本信息
  zg?: ZegoExpressEngine;
  appID = 1739272706;
  serverEnv = 'wss://webliveroom1739272706-api.zego.im/ws';
  // 房间状态
  isLogin = false;
  userID = 'sample' + Date.now();
  roomID = '979797';
  token = '';
  roomState = 'DISCONNECTED';
  showPlayQuality = true;
  // 流状态
  streamID = 'sample' + Date.now();
  publishState = 'NO_PUBLISH';
  localStream: MediaStream | null = null;
  qualityStatsList: QualityStats[] = [];
  cameraInput?: ZegoDeviceInfo;
  micInput?: ZegoDeviceInfo;
  audioState = true;
  videoState = true;
  // 折叠款
  openNames = [];
  // 远端流列表
  get remoteStreamList (): RemoteStreamItem[] {
    const list = Object.values(this.remoteStreamMap)
    return list
  }

  remoteStreamMap: { [key: string]: RemoteStreamItem } = {};

  async playCustomStream (streamID?: string): Promise<void> {
    streamID = streamID || this.playStreamFormValue.streamID
    if (!streamID || !this.isLogin) throw new Error('play error')
    const addItem = this.remoteStreamMap[streamID]
    if (addItem) throw new Error('stream already exist')
    const streamItem: RemoteStreamItem = {
      streamID,
      stream: undefined,
      qualityList: []
    }
    this.$set(this.remoteStreamMap, streamID, streamItem)
    const startTime = performance.now()
    const stream = await this.zg?.startPlayingStream(streamID)
    this.$set(this.playRecordData, streamID, {
      playCostTime: performance.now() - startTime
    })
    this.$set(this.remoteStreamMap[streamID], 'stream', stream)
  }

  initSDK (): void {
    this.serverEnv = localStorage.getItem('express-server') || this.serverEnv
    const appid = localStorage.getItem('express-appid')
    if (appid && !isNaN(parseInt(appid))) {
      this.appID = parseInt(appid)
    }
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    this.zg = (window as any).zg = new ZegoExpressEngine(
      this.appID,
      this.serverEnv
    )
    const { zg } = this
    // (zg as any).zegoWebRTC.setAccess(false)
    zg.setLogConfig({
      // logLevel: 'error',
      logURL: 'wss://weblogger-test.zego.im/log'
    })
    zg.setDebugVerbose(false)
    zg.off('roomOnlineUserCountUpdate')
    zg.on('roomOnlineUserCountUpdate', (roomID: string, count) => {
      console.warn(
        'roomOnlineUserCountUpdate',
        roomID,
        count,
        new Date().toLocaleTimeString()
      )
    })

    zg.off('roomUserUpdate')
    zg.on('roomUserUpdate', (roomID, updateType, userList) => {
      console.warn(
        'roomUserUpdate',
        roomID,
        updateType,
        JSON.stringify(userList)
      )
    })

    zg.off('roomStreamUpdate')
    zg.on('roomStreamUpdate', (roomID, updateType, streamList) => {
      console.log('roomStreamUpdate', roomID, updateType, streamList)
      if (!this.demoConfig.autoPlay) {
        return
      }
      if (updateType === 'ADD') {
        streamList.forEach(async ({ streamID }) => {
          this.playCustomStream(streamID)
        })
      } else {
        const delStreamIDs = streamList.map((item) => item.streamID)
        this.remoteStreamList.forEach(async ({ streamID }) => {
          if (delStreamIDs.includes(streamID)) {
            zg.stopPlayingStream(streamID)
            this.$delete(this.remoteStreamMap, streamID)
          }
        })
      }
    })

    zg.off('roomStateUpdate')
    zg.on('roomStateUpdate', (roomID, state, error, extendedData) => {
      console.warn('roomStateUpdate', roomID, state, error, extendedData)
      this.roomState = state
      // if (state == "DISCONNECTED") debugger;
    })

    zg.off('publisherStateUpdate')
    zg.on('publisherStateUpdate', async (result) => {
      if (result.state === 'PUBLISHING') {
        if (this.recordData.publishStartTime) {
          this.recordData.publishCostTime =
            performance.now() - this.recordData.publishStartTime
          this.recordData.publishStartTime = 0
        }
      }
      console.warn('publisherStateUpdate', JSON.stringify(result))
      this.publishState = result.state
    })

    zg.off('playerStateUpdate')
    zg.on('playerStateUpdate', (result) => {
      console.warn('playerStateUpdate', JSON.stringify(result))
    })

    zg.on('publishQualityUpdate', (streamID, stats) => {
      const { video } = stats
      const { videoBitrate, videoFPS, frameWidth, frameHeight } = video
      this.qualityStatsList.push({
        bitrate: videoBitrate,
        width: frameWidth,
        height: frameHeight,
        frameRate: videoFPS,
        time: Date.now()
      })
      console.warn(
        'publish ' +
          streamID +
          ' ' +
          `videoBitRate: ${stats.video.videoBitrate} audioBitRate: ${stats.audio.audioBitrate} videoFPS: ${stats.video.videoFPS} audioFPS: ${stats.audio.audioFPS}`
      )
    })

    zg.off('playQualityUpdate')
    zg.on('playQualityUpdate', (streamID, stats) => {
      const playStream = this.remoteStreamMap[streamID]
      if (!playStream) return
      const { video } = stats
      const { videoBitrate, videoFPS, frameWidth, frameHeight } = video
      playStream.qualityList.push({
        bitrate: videoBitrate,
        width: frameWidth,
        height: frameHeight,
        frameRate: videoFPS,
        time: Date.now()
      })
      console.warn(
        'playQualityUpdate ' +
          streamID +
          ' ' +
          `videoBitRate: ${stats.video.videoBitrate} audioBitRate: ${stats.audio.audioBitrate} videoFPS: ${stats.video.videoFPS} audioFPS: ${stats.audio.audioFPS}`
      )
      console.log('playQualityUpdate', stats)
    })

    zg.off('screenSharingEnded')
    zg.on('screenSharingEnded', (stream) => console.warn(stream))
    // zg.setSoundLevelDelegate(true, 1000);
    zg.off('soundLevelUpdate')
    zg.on('soundLevelUpdate', (list) =>
      console.warn(
        'soundLevelUpdate',
        list.map((item) => item.soundLevel)
      )
    )
    zg.off('capturedSoundLevelUpdate')
    zg.on('capturedSoundLevelUpdate', (level: number) =>
      console.warn('capturedSoundLevelUpdate', level)
    )

    zg.off('remoteCameraStatusUpdate')
    zg.on('remoteCameraStatusUpdate', (id, status) => {
      console.warn('remoteCameraStatusUpdate', id, status)
      this.remoteStreamMap[id] &&
        (this.remoteStreamMap[id].videoStatus = status)
    })
    zg.off('remoteMicStatusUpdate')
    zg.on('remoteMicStatusUpdate', (id, status) => {
      console.warn('remoteMicStatusUpdate', id, status)
      this.remoteStreamMap[id] &&
        (this.remoteStreamMap[id].audioStatus = status)
    })

    // zg.off('beautyEffectOverload')
    // zg.on('beautyEffectOverload', (stream) => {
    //   console.warn('beautyEffectOverload  美颜过载', stream)
    //   // zg.setBeautyEffect(stream, false, {});
    // })
    zg.off('tokenWillExpire')
    zg.on('tokenWillExpire', (roomID) => {
      console.warn('tokenWillExpire', roomID)
      this.$message.warning('tokenWillExpire, roomID: ' + roomID)
    })
  }

  async loginRoomAndPublish (): Promise<void> {
    await this.loginRoom()
    await this.startPublishing()
  }

  async loginRoom (): Promise<void> {
    const { zg } = this
    if (!zg) return
    let token = this.genToken

    if (!token) {
      // token = await getToken03(this.appID, this.userID, this.roomID, 6000)
      token = await getToken(this.appID, this.userID)
    }
    this.isLogin = await zg
      .loginRoom(this.roomID, token, {
        userID: this.userID
      })
      .catch((error) => {
        this.$message.error(JSON.stringify(error))
        throw error
      })
  }

  genToken = '';
  tokenExpireTime = 60;
  async generateToken (): Promise<void> {
    const expire = isNaN(this.tokenExpireTime)
      ? undefined
      : this.tokenExpireTime
    this.genToken = await getToken03(
      this.appID,
      this.userID,
      this.roomID,
      expire
    )
  }

  renewToken (): void {
    if (!this.zg) return
    this.zg.renewToken(this.genToken)
  }

  customMediaEl?: HTMLMediaElement;
  async startPreview (): Promise<void> {
    console.log('startPreview')
    const { zg } = this
    if (!zg || this.localStream) throw new Error('stream exist')
    const {
      videoQuality,
      width,
      height,
      frameRate,
      bitrate,
      videoOptimizationMode,
      source,
      keyFrameInterval
    } = this.videoQuality
    this.videoState = this.cameraInput?.deviceID !== undefined
    this.audioState = this.micInput?.deviceID !== undefined
    console.log('device', this.cameraInput?.deviceID, this.micInput?.deviceID)
    const streamConfig: ZegoLocalStreamConfig = {}
    if (source === 'camera') {
      streamConfig.camera = {
        videoQuality,
        width,
        height,
        frameRate,
        bitrate,
        video: this.videoState ? undefined : false,
        videoInput: this.cameraInput?.deviceID,
        audio: this.audioState ? undefined : false,
        audioInput: this.micInput?.deviceID,
        videoOptimizationMode,
        keyFrameInterval: keyFrameInterval || undefined
      }
    } else if (source === 'screen') {
      streamConfig.screen = {
        videoQuality,
        width,
        height,
        frameRate,
        bitrate,
        audio: this.audioState,
        videoOptimizationMode,
        keyFrameInterval: keyFrameInterval || undefined
      }
    } else if (source === 'custom' && this.customMediaEl) {
      streamConfig.custom = {
        source: this.customMediaEl,
        bitrate,
        videoOptimizationMode,
        keyFrameInterval: keyFrameInterval || undefined
      }
    } else {
      return alert('预览失败，参数错误')
    }
    console.log('streamConfig', streamConfig)
    try {
      this.localStream = await zg.createStream(streamConfig)
    } catch (error) {
      alert(JSON.stringify(error))
    }
  }

  async startPublishing (): Promise<void> {
    console.log('startPublishing')
    setStreamQuality(this.videoQuality)
    const { zg } = this
    if (!zg) return
    if (!this.localStream) {
      await this.startPreview()
    }
    if (this.localStream) {
      try {
        this.recordData.publishStartTime = performance.now()
        zg.startPublishingStream(this.streamID, this.localStream)
      } catch (error) {
        this.$message.error(JSON.stringify(error))
        throw error
      }
    }
    this.qualityStatsList = []
  }

  stopPublishing (): void {
    const { zg } = this
    if (!zg) return
    if (this.localStream) {
      zg.stopPublishingStream(this.streamID)
      zg.destroyStream(this.localStream)
      this.localStream = null
    }
  }

  async logoutRoom (): Promise<void> {
    const { zg } = this
    if (!zg) return
    this.remoteStreamList.forEach((stream) => {
      zg.stopPlayingStream(stream.streamID)
      this.$delete(this.remoteStreamMap, stream.streamID)
    })
    this.stopPublishing()
    zg.logoutRoom(this.roomID)
    this.isLogin = false
  }

  resetEnv (): void {
    localStorage.setItem('express-server', this.serverEnv + '')
    localStorage.setItem('express-appid', this.appID + '')
  }

  onVideoStateChange (val: boolean): void {
    this.videoState = val
  }

  onAudioStateChange (val: boolean): void {
    this.audioState = val
  }

  onCameraChange (val?: ZegoDeviceInfo): void {
    this.cameraInput = val
    console.log('onCameraChange', this.cameraInput)
  }

  onMicChange (val?: ZegoDeviceInfo): void{
    this.micInput = val
    console.log('onMicChange', this.micInput, val)
  }

  mounted (): void {
    document.title = 'ZEGO RTC'
    if (document.body.clientWidth < 450) this.columnNumber = 1

    this.videoQuality = getStreamQuality() || this.videoQuality
    this.demoConfig = getDemoConfig() || this.demoConfig
  }

  created (): void {
    this.initSDK()
  }

  destroyed (): void {
    this.logoutRoom()
  }
}
