import { Vue, Component } from 'vue-property-decorator'
import { setDemoConfig } from './streamUtils'
@Component
export default class RtcDemoMixin extends Vue {
  // data
  videoQuality: {
    videoQuality?: 2 | 1 | 3 | 4 | undefined;
    width?: number;
    height?: number;
    bitrate?: number;
    frameRate?: number;
    videoOptimizationMode?: 'motion' | 'detail' | undefined;
    source: 'custom' | 'screen' | 'camera';
    keyFrameInterval?: number;
  } = {
    videoQuality: 4,
    width: 320,
    height: 240,
    bitrate: 800,
    frameRate: 15,
    videoOptimizationMode: undefined,
    source: 'camera'
  };

  demoConfig: any={
    autoPlay: true
  }

  // 侧拉框
  drawerVisible = false;

  playStreamFormValue: {
    streamID?: string;
    index?: number;
  } = {
    streamID: undefined
  };

  // 通话耗时统计
  recordData: any = {
    publishStartTime: 0,
    publishCostTime: 0
  };

  playRecordData: any = {};

  onAutoPlayStreamChange (): void {
    setDemoConfig(this.demoConfig)
    location.reload()
  }
}
