













































































import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import TRTC from 'trtc-js-sdk'

@Component({})
export default class DeviceManager extends Vue {
  @Prop() localStream!: any;
  formValue: {
    cameraInput?: string;
    micInput?: string;
  } = {
    cameraInput: undefined,
    micInput: undefined
  };

  micInputList: MediaDeviceInfo[] = [];
  cameraInputList: MediaDeviceInfo[] = [];

  // 画面状态
  isVideoOpen = false;
  isAudioOpen = false;
  isMicOpen = false;
  async enumDevices (): Promise<void> {
    const deviceList = await TRTC.getDevices()
    if (deviceList && deviceList.length) {
      this.micInputList = deviceList
        .filter((item) => item.kind === 'audioinput')
        .map((item) => {
          return item
        })
      this.cameraInputList = deviceList
        .filter((item) => item.kind === 'videoinput')
        .map((item) => {
          return item
        })
      this.formValue.cameraInput = this.cameraInputList[0].deviceId
      this.formValue.micInput = this.micInputList[0].deviceId
    }
  }

  async useCamera (): Promise<void> {
    if (this.formValue.cameraInput) {
      try {
        await this.localStream.switchDevice('video', this.formValue.cameraInput)
      } catch (error) {
        alert(JSON.stringify(error))
      }
    } else {
      alert('change device failed')
    }
  }

  async useMic (): Promise<void> {
    if (this.formValue.micInput) {
      try {
        await this.localStream.switchDevice('audio', this.formValue.micInput)
      } catch (error) {
        alert(JSON.stringify(error))
      }
    } else {
      alert('change device failed')
    }
  }

  muteVideo (): void {
    try {
      if (this.isVideoOpen) {
        this.localStream.muteVideo()
      } else {
        this.localStream.unmuteVideo()
      }
      this.isVideoOpen = !this.isVideoOpen
    } catch (error) {
      alert(JSON.stringify(error))
    }
  }

  muteAudio (): void {
    if (this.isAudioOpen) {
      this.localStream.muteAudio()
    } else {
      this.localStream.unmuteAudio()
    }
    this.isAudioOpen = !this.isAudioOpen
  }

  @Watch('isVideoOpen')
  onVideoStateChange (val: boolean): void {
    this.$emit('videoStateChange', val)
  }

  @Watch('isAudioOpen')
  onAudioStateChange (val: boolean): void {
    this.$emit('audioStateChange', val)
  }

  @Watch('formValue.cameraInput')
  onCameraChange (val: string): void {
    const device = this.cameraInputList.find((item) => item.deviceId === val)
    this.$emit('cameraChange', device)
  }

  @Watch('formValue.micInput')
  onMicChange (val: string): void {
    const device = this.micInputList.find((item) => item.deviceId === val)
    this.$emit('micChange', device)
  }

