













































































import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import VERTC, { IRTCEngine } from '@volcengine/rtc'

export interface DeviceInfo {
  deviceId: string;
  groupId?: string;
  label: string;
}

@Component({})
export default class DeviceManager extends Vue {
  @Prop() videoTrack!: MediaStreamTrack;
  @Prop() audioTrack!: MediaStreamTrack;
  @Prop() rtc!: IRTCEngine;
  formValue: {
    cameraInput?: string;
    micInput?: string;
  } = {
    cameraInput: undefined,
    micInput: undefined
  };

  micInputList: DeviceInfo[] = [];
  cameraInputList: DeviceInfo[] = [];

  // 画面状态
  isVideoOpen = false;
  isAudioOpen = false;
  isMicOpen = false;
  async enumDevices (): Promise<void> {
    this.micInputList = (await VERTC.enumerateAudioCaptureDevices()).map((item) => {
      return item
    })
    this.cameraInputList = (await VERTC.enumerateVideoCaptureDevices()).map((item) => {
      return item
    })

    this.formValue.cameraInput = this.cameraInputList[0]?.deviceId
    this.formValue.micInput = this.micInputList[0]?.deviceId
  }

  async useCamera (): Promise<void> {
    if (this.videoTrack && this.formValue.cameraInput) {
      await this.rtc.stopVideoCapture()
      await this.rtc.startVideoCapture(this.formValue.cameraInput)
      this.$emit('videoTrackChange')
    } else {
      alert('change device failed ' + this.formValue.cameraInput)
    }
  }

  async useMic (): Promise<void> {
    if (this.audioTrack && this.formValue.micInput) {
      await this.rtc.stopAudioCapture()
      await this.rtc.startAudioCapture(this.formValue.micInput)
      this.$emit('audioTrackChange')
    } else {
      alert('change device failed')
    }
  }

  muteVideo (): void {
    try {
      this.videoTrack && (this.videoTrack.enabled = !this.isVideoOpen)
      this.isVideoOpen = !this.isVideoOpen
    } catch (error) {
      alert(JSON.stringify(error))
    }
  }

  muteAudio (): void {
    try {
      this.audioTrack && (this.audioTrack.enabled = !this.isAudioOpen)
      this.isAudioOpen = !this.isAudioOpen
    } catch (error) {
      alert(JSON.stringify(error))
    }
  }

  @Watch('isVideoOpen')
  onVideoStateChange (val: boolean): void {
    this.$emit('videoStateChange', val)
  }

  @Watch('isAudioOpen')
  onAudioStateChange (val: boolean): void {
    this.$emit('audioStateChange', val)
  }

  @Watch('formValue.cameraInput')
  onCameraChange (val: string): void {
    const device = this.cameraInputList.find((item) => item.deviceId === val)
    this.$emit('cameraChange', device)
  }

  @Watch('formValue.micInput')
  onMicChange (val: string): void {
    const device = this.micInputList.find((item) => item.deviceId === val)
    this.$emit('micChange', device)
  }

