








import { Component, Vue, Prop, Watch, Ref } from 'vue-property-decorator'
import {
  IRTCEngine,
  StreamIndex
} from '@volcengine/rtc'

@Component({})
export default class MediaPlayer extends Vue {
  @Prop() private rtc?: IRTCEngine;
  @Prop() private userId?: string;
  @Prop() private videoTrack?: MediaStreamTrack;
  @Prop() private audioTrack?: MediaStreamTrack;
  @Prop({ default: false }) private isLocal?: boolean;
  @Prop({ default: false }) private source?: string;
  @Ref('player') readonly player!: HTMLDivElement;

  @Watch('videoTrack', { })
  async onVideoChange (val: MediaStreamTrack):Promise<void> {
    console.log('1111 onVideoChange', this.player, val)
    if (!this.player || !val) return
    this.renderPlayer()
    this.setVideoControls()
  }

  async renderPlayer (): Promise<void> {
    const index = this.source === 'screen' ? StreamIndex.STREAM_INDEX_SCREEN : StreamIndex.STREAM_INDEX_MAIN
    console.log('renderPlayer', this.source, index)
    try {
      if (this.isLocal) {
        await this.rtc?.setLocalVideoPlayer(index, {
          userId: this.userId || '',
          renderDom: this.player
        })
      } else {
        await this.rtc?.setRemoteVideoPlayer(index, {
          userId: this.userId || '',
          renderDom: this.player
        })
      }
    } catch (error) {
      alert('render failed: ' + JSON.stringify(error))
      throw error
    }
  }

  @Watch('audioTrack', { })
  async onAudioChange (val: MediaStreamTrack):Promise<void> {
    console.log('1111 onAudioChange', this.player, val)
    if (!this.player || !val) return

    this.renderPlayer()
  }

  setVideoControls (): void {
    const videos = document.querySelectorAll('video')
    videos.forEach((item) => {
      item.setAttribute('controls', 'true')
    })
  }

  async mounted ():Promise<void> {
    console.log('1111 mounted', this.player, this.videoTrack, this.audioTrack)
    if (this.player && this.videoTrack) {
      console.log('1111 onVideoChange')
      this.renderPlayer()
      this.setVideoControls()
    }
    if (this.player && this.audioTrack) {
      console.log('1111 onAudioChange')
      this.renderPlayer()
    }
  }
}

