export function getStreamQuality (): any {
  const quality = localStorage.getItem('stream-quality')
  if (quality) {
    try {
      return JSON.parse(quality)
    } catch (error) {
      localStorage.setItem('stream-quality', '{}')
    }
  }
}

export function setStreamQuality (videoQuality = {}) :void{
  localStorage.setItem('stream-quality', JSON.stringify(videoQuality))
}

