



































































import { Component, Prop, Vue } from 'vue-property-decorator'
import dayjs from 'dayjs'
interface QualityStats {
  bitrate: number;
  width: number;
  height: number;
  frameRate: number;
  time: number;
}

@Component({})
export default class PlayCard extends Vue {
  showList = [];
  @Prop({ default: false }) isPublish!: boolean;
  @Prop() streamID!: number | string;
  @Prop() videoStatus?: string;
  @Prop() audioStatus?: string;
  @Prop({ default: () => [] }) qualityList!: QualityStats[];
  @Prop({ default: () => ({}) }) recordData!: any;
  get currentQuality (): QualityStats {
    const quality = this.qualityList[this.qualityList.length - 1]
    return (
      quality || {
        bitrate: 0,
        width: 0,
        height: 0,
        frameRate: 0
      }
    )
  }

  tableColumns: {
    prop: string;
    label: string;
    // eslint-disable-next-line @typescript-eslint/ban-types
    formatter?: Function;
    width?: number;
    sortable?: boolean;
  }[] = [
    {
      prop: 'time',
      label: '时间',
      formatter (row: QualityStats): string {
        return dayjs(row.time).format('HH:mm:ss')
      },
      width: 80,
      sortable: true
    },
    {
      prop: 'width',
      label: '分辨率',
      formatter (row: QualityStats): string {
        return row.width + ' , ' + row.height
      }
    },
    {
      prop: 'frameRate',
      label: '帧率',
      formatter (row: QualityStats, columns: QualityStats, val: number): number {
        return Math.round(val * 1e3) / 1e3
      }
    },
    {
      prop: 'bitrate',
      label: '码率',
      formatter (row: QualityStats, columns: QualityStats, val: number): number {
        return Math.round(val * 1e3) / 1e3
      }
    }
  ];

  maxWidth = 1300;
  mounted (): void {
    this.maxWidth = document.body.clientWidth
  }
}
