











































































import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import { ZegoExpressEngine } from 'zego-express-engine-webrtc'
import { ZegoDeviceInfo } from 'zego-express-engine-webrtc/sdk/code/zh/ZegoExpressEntity.web'
interface DeviceInfo extends ZegoDeviceInfo {
  deviceName: string;
}
@Component({})
export default class DeviceManager extends Vue {
  @Prop() zg!: ZegoExpressEngine;
  @Prop() localStream!: MediaStream;
  formValue: {
    cameraInput?: string;
    micInput?: string;
  } = {
    cameraInput: undefined,
    micInput: undefined
  };

  micInputList: DeviceInfo[] = [];
  cameraInputList: DeviceInfo[] = [];

  // 画面状态
  isVideoOpen = false;
  isAudioOpen = false;
  isMicOpen = false;
  async enumDevices (): Promise<void> {
    const { zg } = this
    if (!zg) return
    const deviceInfo = await zg.enumDevices()

    if (deviceInfo) {
      this.micInputList = deviceInfo.microphones.map((item, index) => {
        if (!item.deviceName) {
          item.deviceName = 'microphone' + index
        }
        return item
      })
      this.cameraInputList = deviceInfo.cameras.map((item, index) => {
        if (!item.deviceName) {
          item.deviceName = 'camera' + index
        }
        console.log('camera: ' + item.deviceName)
        return item
      })
      this.formValue.cameraInput = this.cameraInputList[0].deviceID
      this.formValue.micInput = this.micInputList[0].deviceID
    }

    // this.micInputList.push({
    //   deviceName: '',
    //   deviceID: '0'
    // })
    // this.cameraInputList.push({
    //   deviceName: '',
    //   deviceID: '0'
    // })
  }

  async useCamera (): Promise<void> {
    this.formValue.cameraInput &&
      (await this.zg.useVideoDevice(
        this.localStream,
        this.formValue.cameraInput
      ))
  }

  async useMic (): Promise<void> {
    this.formValue.micInput &&
      (await this.zg.useAudioDevice(this.localStream, this.formValue.micInput))
  }

  muteVideo (): void {
    this.zg.mutePublishStreamVideo(this.localStream, this.isVideoOpen)
    this.isVideoOpen = !this.isVideoOpen
  }

  muteAudio (): void {
    this.zg.mutePublishStreamAudio(this.localStream, this.isAudioOpen)
    this.isAudioOpen = !this.isAudioOpen
  }

  async checkRequirements (): Promise<void> {
    const res = await this.zg.checkSystemRequirements()
    alert(JSON.stringify(res))
  }

  @Watch('isVideoOpen')
  onVideoStateChange (val: boolean): void {
    this.$emit('videoStateChange', val)
  }

  @Watch('isAudioOpen')
  onAudioStateChange (val: boolean): void {
    this.$emit('audioStateChange', val)
  }

  @Watch('formValue.cameraInput')
  onCameraChange (val: string): void {
    const device = this.cameraInputList.find((item) => item.deviceID === val)
    this.$emit('cameraChange', device)
  }

  @Watch('formValue.micInput')
  onMicChange (val: string): void {
    const device = this.micInputList.find((item) => item.deviceID === val)
    this.$emit('micChange', device)
  }

  @Watch('zg', { immediate: true })
  async onRtcChange (zg: ZegoExpressEngine): Promise<void> {
    if (!zg) return
    zg.on('deviceError', async (code, deviceName) => {
      console.log('deviceError', code, deviceName)
    })
    zg.on('audioDeviceStateChanged', (updateType, deviceType, deviceInfo) => {
      if (deviceType === 'Input') {
        if (updateType === 'ADD') {
          this.micInputList.push(deviceInfo)
        } else {
          this.micInputList = this.micInputList.filter((item) => {
            return item.deviceID !== deviceInfo.deviceID
          })
        }
      }
    })
    zg.on('videoDeviceStateChanged', (updateType, deviceInfo) => {
      if (updateType === 'ADD') {
        this.cameraInputList.push(deviceInfo)
      } else {
        this.cameraInputList = this.cameraInputList.filter((item) => {
          return item.deviceID !== deviceInfo.deviceID
        })
      }
    })

    // await zg.checkSystemRequirements('camera')
    // await zg.checkSystemRequirements('microphone')
    this.enumDevices()
  }
}
