  startStatsTimer (): void {
    clearInterval(this.statsTimer)
    this.statsTimer = window.setInterval(async () => {
      if (!this.rtc) return
      if (this.localStream && this.publishState === 'CONNECTED') {
        const videoStats = await this.rtc.getLocalVideoStats()
        // const audioStats = this.localTracks.audioTrack ? this.rtc.getLocalAudioStats() : {}
        const { frameWidth, frameHeight, framesSent, bytesSent } =
          videoStats[this.userID]

        const now = Date.now()
        let bitrate = 0
        let frameRate = 0
        if (now > this.currentQualityStats.time) {
          console.log(
            'bytesSent',
            bytesSent - this.currentStats.bytesSent,
            bytesSent,
            this.currentStats.bytesSent
          )
          const duration = (now - this.currentQualityStats.time) / 1000
          bitrate =
            ((bytesSent - this.currentStats.bytesSent) * 8) / 1000 / duration
          frameRate = (framesSent - this.currentStats.framesSent) / duration
        } else {
        }
        this.qualityStatsList.push({
          bitrate,
          width: frameWidth,
          height: frameHeight,
          frameRate,
          time: now
        })
        this.currentStats = {
          framesSent,
          bytesSent,
          time: now
        }
      }
      if (this.playingRemoteStreamList.length) {
        const remoteStats = await this.rtc.getRemoteVideoStats('main')
        this.playingRemoteStreamList.forEach((item) => {
          const videoStats = remoteStats[item.streamID]
          if (videoStats) {
            const { bytesReceived, frameHeight, frameWidth, framesDecoded } =
              videoStats

            const now = Date.now()
            let bitrate = 0
            let frameRate = 0
            if (now > item.currentStats.time) {
              const duration = (now - item.currentStats.time) / 1000
              bitrate =
                ((bytesReceived - item.currentStats.bytesReceived) * 8) /
                1000 /
                duration
              frameRate =
                (framesDecoded - item.currentStats.framesDecoded) / duration
            } else {
            }
            item.qualityList.push({
              bitrate,
              frameRate,
              width: frameWidth,
              height: frameHeight,
              time: now
            })
            item.currentStats = {
              framesDecoded,
              bytesReceived,
              time: now
            }
          }
        })
      }
    }, 3000)
  }

