  async playCustomStream (streamID?: string): Promise<void> {
    streamID = streamID || this.playStreamFormValue.streamID
    if (!streamID || !this.isLogin) throw new Error('play error')
    const addItem = this.playingRemoteStreamList.find(
      (item) => item.streamID === streamID
    )
    if (addItem) throw new Error('stream already exist')

    const remoteStream = this.remoteStreamMap[streamID]

    this.$set(this.playRecordData, remoteStream.getUserId(), {
      playStartTime: performance.now()
    })
    // 订阅远端流
    this.rtc.subscribe(remoteStream)
  }

  async loginRoomAndPublish (): Promise<void> {
    await this.loginRoom()
    await this.startPublishing()
  }

  async loginRoom (): Promise<void> {
    const { rtc } = this
    if (!rtc) return
    await rtc.join({ roomId: this.roomID })
    this.isLogin = true
    this.startStatsTimer()
  }

  customMediaEl?: HTMLMediaElement;
  async startPreview (): Promise<void> {
    console.log('startPreview')
    const { rtc } = this
    if (!rtc) return

    const {
      // videoQuality,
      width,
      height,
      frameRate,
      bitrate,
      videoOptimizationMode,
      source
    } = this.videoQuality

    this.videoState = !!this.cameraInput?.deviceId
    this.audioState = !!this.micInput?.deviceId
    let localStream: LocalStream | null = null
    try {
      if (source === 'camera') {
        localStream = TRTC.createStream({
          userId: this.userID,
          // cameraId: this.cameraInput?.deviceId,
          // microphoneId: this.micInput?.deviceId,
          audio: true,
          video: true
        })
        localStream.setVideoProfile({
          width: width || 640, // 视频宽度
          height: height || 480, // 视频高度
          frameRate: frameRate || 10, // 帧率
          bitrate: bitrate || 500 // 比特率 kbps
        })
        // localStream.setAudioVolume(1)
        // if (this.timer) {
        //   clearInterval(this.timer)
        // }
        // this.timer = window.setInterval(() => {
        // // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // // @ts-ignore
        //   const volume = localStream?.getAudioLevel()
        //   console.warn('volume', volume)
        // }, 1000)
      } else if (source === 'screen') {
        localStream = TRTC.createStream({
          userId: this.userID,
          screen: true,
          screenAudio: true
        })
        localStream.setScreenProfile({
          width: width || 640, // 视频宽度
          height: height || 480, // 视频高度
          frameRate: frameRate || 10, // 帧率
          bitrate: bitrate || 500 // 比特率 kbps
        })
      } else if (source === 'custom' && this.customMediaEl) {
        const stream = (this.customMediaEl as any).captureStream()
        const videoTrack = stream.getVideoTracks()[0]
        const audioTrack = stream.getAudioTracks()[0]
        localStream = TRTC.createStream({
          userId: this.userID,
          audioSource: audioTrack,
          videoSource: videoTrack
        })
        localStream.setVideoProfile({
          width: width || 640, // 视频宽度
          height: height || 480, // 视频高度
          frameRate: frameRate || 10, // 帧率
          bitrate: bitrate || 500 // 比特率 kbps
        })
      } else {
        return alert('预览失败，参数错误')
      }
      // // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // // @ts-ignore
      // localStream.setAudioCaptureVolume(150);
      // (window as any).localStream1 = localStream
    } catch (error) {
      alert(error + '')
      throw error
    }

    if (localStream) {
      localStream.on('connection-state-changed', ({ state }: any) => {
        this.publishState = state
        if (state === 'CONNECTED') {
          this.currentStats.time = Date.now()
        }
      })
      await localStream.initialize()
      videoOptimizationMode &&
        localStream.setVideoContentHint(videoOptimizationMode)
      // localStream = rtcBeautyPlugin.generateBeautyStream(localStream)
      this.localStream = localStream
    }
  }

  async startPublishing (): Promise<void> {
    setStreamQuality(this.videoQuality)
    console.log('startPublishing')
    const { rtc } = this
    if (!rtc) return
    if (!this.localStream) {
      await this.startPreview()
    }

    const startTime = performance.now()
    this.localStream && (await rtc.publish(this.localStream))
    this.$set(
      this.recordData,
      'publishCostTime',
      performance.now() - startTime
    )
    // this.publishState = 'PUBLISHING'
    this.qualityStatsList = []
  }

  async stopPublishing (): Promise<void> {
    const { rtc } = this
    if (!rtc) return
    if (this.localStream) {
      if (this.publishState === 'CONNECTED') {
        await rtc.unpublish(this.localStream)
      }
      this.localStream.stop()
      this.localStream.close()
      this.localStream = null
    }
  }

  async logoutRoom (): Promise<void> {
    const { rtc } = this
    if (!rtc) return
    // this.playingRemoteStreamList.forEach((stream) => {
    //   rtc.stopPlayingStream(stream.streamID)
    // })
    this.playingRemoteStreamList = []
    this.remoteStreamMap = {}
    await this.stopPublishing()
    await rtc.leave()
    this.isLogin = false
    this.roomState = 'DISCONNECTED'
    // this.publishState = 'NO_PUBLISH'
  }

  async mounted (): Promise<void> {
    document.title = 'TRTC'
    if (document.body.clientWidth < 450) this.columnNumber = 1
    const secret =
      (this.$route.query.secret as string) ||
      localStorage.getItem('trtc-secret') ||
      ''
    const appid =
      (this.$route.query.appID as string) ||
      localStorage.getItem('trtc-appid') ||
      ''
    // this.$router.params =
    this.$router.replace({ query: { secret, appID: appid } }).catch(() => ({}))
    this.secretKey = secret || ''
    this.appID = appid ? parseInt(appid) : 0

    this.videoQuality = getStreamQuality() || this.videoQuality
    this.demoConfig = getDemoConfig() || this.demoConfig
  }

  muteVideo (): void {
    this.localStream.muteVideo()
  }

  unmuteVideo (): void {
    this.localStream.unmuteVideo()
  }

  onVideoStateChange (val: boolean): void {
    this.videoState = val
  }

  onAudioStateChange (val: boolean): void {
    this.audioState = val
  }

  onCameraChange (val?: MediaDeviceInfo): void {
    this.cameraInput = val
    console.log('onCameraChange', this.cameraInput)
  }

  onMicChange (val?: MediaDeviceInfo): void {
    this.micInput = val
    console.log('onMicChange', this.micInput)
  }

  onAutoPlayChange (): void {
    setDemoConfig(this.demoConfig)
    location.reload()
  }

  destroyed (): void {
    this.logoutRoom()
  }
}
