








import { Component, Vue, Prop, Watch, Ref } from 'vue-property-decorator'
import {
  ILocalVideoTrack,
  IRemoteVideoTrack,
  ILocalAudioTrack,
  IRemoteAudioTrack
} from 'agora-rtc-sdk-ng'
export interface VideoPlayerProps {
  videoTrack: ILocalVideoTrack | IRemoteVideoTrack | undefined;
  audioTrack: ILocalAudioTrack | IRemoteAudioTrack | undefined;
}

@Component({})
export default class Agora extends Vue {
  @Prop() private isPusher!:boolean;
  @Prop() private videoTrack!: ILocalVideoTrack | IRemoteVideoTrack;
  @Prop() private audioTrack!: ILocalAudioTrack | IRemoteAudioTrack;
  @Ref('player') readonly player!: HTMLVideoElement;

  @Watch('videoTrack', { })
  onVideoChange (val: ILocalVideoTrack | IRemoteVideoTrack):void {
    console.log('1111 onVideoChange', this.player, val)
    if (!this.player || !val) return
    val.play(this.player)
    this.setVideoControls()
  }

  @Watch('audioTrack', { })
  onAudioChange (val: ILocalAudioTrack | IRemoteAudioTrack):void {
    console.log('1111 onAudioChange', this.player, val)
    if (!this.player || !val) return
    val.play()
  }

  setVideoControls (): void {
    const videos = document.querySelectorAll('video')
    videos.forEach((item) => {
      item.setAttribute('controls', 'true')
    })
  }

  mounted ():void {
    console.log('1111 mounted', this.player)
    console.log('1111 onVideoChange', this.player, this.videoTrack)
    if (!this.player || !this.videoTrack) return
    this.videoTrack.play(this.player)
    this.setVideoControls()
    console.log('1111 onAudioChange', this.player, this.audioTrack)
    if (!this.player || !this.audioTrack) return
    this.audioTrack.play()
    // console.log('soundLevel', this.audioTrack?.getVolumeLevel())
    // window.setInterval(() => {
    //   console.log('soundLevel', !!this.isPusher, this.audioTrack?.getVolumeLevel())
    // }, 1000)
  }
}

